(function() {
  'use strict';

  angular
    .module('geldzakenAngular', ['ngResource', 'ui.router', 'ui.router.title', 'ngMaterial', 'ngMessages', 'toastr', 'LocalStorageModule', 'angularMoment', 'dtrw.bcrypt']);

})();

(function() {
  'use strict';

  angular
      .module('geldzakenAngular')
      .service('userService', userService);

  /** @ngInject */
  function userService($mdDialog, $http, authentication, api, localStorageService) {
    var service = this;

    service.signup = function (user, callback) {
      // Stuur het emailadres en wachtwoord naar /login
      $http.post(api.public + '/signup', {
        email: user.email,
        password: user.password,
        name: user.name,
        bankAccount: user.bankAccount
      })
      .success(function (data, status, headers, config) {
        callback();
      })
      .error(function (data, status, headers, config) {
        throw new Error("Error "+status+": "+data);
      });
    };

    service.update = function (user, callback) {
      // Stuur het emailadres en wachtwoord naar /login
      $http.put(api.private + '/users/' + service.me().email, {
        password: user.password,
        name: user.name,
        bankAccount: user.bankAccount
      })
      .success(function (data, status, headers, config) {
        localStorageService.set('me', data);
        callback();
      })
      .error(function (data, status, headers, config) {
        $mdDialog.show(
          $mdDialog.alert()
            .clickOutsideToClose(true)
            .title('Can\'t save settings!')
            .textContent(data)
            .ariaLabel('Setting Error')
            .ok('Okay!')
        );
      });
    };

    service.delete = function () {
      $http.delete(api.private + '/users/' + service.me().email)
      .success(function (data, status, headers, config) {
        authentication.logout();
      })
      .error(function (data, status, headers, config) {
        $mdDialog.show(
          $mdDialog.alert()
            .clickOutsideToClose(true)
            .title('Can\'t delete account!')
            .textContent(data)
            .ariaLabel('Deletion error')
            .ok('Okay!')
        );
      });
    };

    service.me = function () {
      return localStorageService.get('me');
    };

    return service;
  }

})();

