(function() {
  'use strict';

  angular
    .module('geldzakenAngular')
    .controller('SettleController', SettleController);

  /** @ngInject */
  function SettleController($http, $state, api, $mdDialog, groupService, group) {
    var ctrl = this;

    ctrl.group = group;

    ctrl.settle = function (ev) {
      var confirm = $mdDialog.confirm()
            .title('Are you sure?')
            .textContent('Settling a group cannot be reverted.')
            .ariaLabel('Confirm')
            .targetEvent(ev)
            .ok('Settle')
            .cancel('Cancel');
      $mdDialog.show(confirm).then(function() {
        $http.get(api.private + '/groups/' + group._id + '/settle')
        .success(function (data, status, headers, config) {
          console.log(data);
          $state.reload();
          $mdDialog.show($mdDialog.alert()
            .title('Group was settled!')
            .ariaLabel('Confirmation')
            .ok('Okay!')
          ).then(function() {
            $state.go('private.group.participants');
          });
        })
        .error(function (data, status, headers, config) {
          throw new Error("Error "+status+": "+data);
        });
      });
    };

  }

})();

(function() {
  'use strict';

  angular
    .module('geldzakenAngular')
    .controller('GroupSettingsController', GroupSettingsController);

  /** @ngInject */
  function GroupSettingsController($state, $mdDialog, groupService, memberService, group) {
    var vm = this;

    vm.group = group;

    vm.save = function () {
      vm.group.$update(function (data) {
        $mdDialog.show(
          $mdDialog.alert()
            .clickOutsideToClose(true)
            .title('Settings were saved!')
            .ariaLabel('Setting Confirmation')
            .ok('Okay!')
        );
        $state.reload();
      }, function (data) {
        throw new Error(data.message);
      });
    };

    vm.leave = function () {
      memberService.Member.delete({ group: group.id }, function (data) {
        console.log('Success!');
        console.log(data);

        $mdDialog.show(
          $mdDialog.alert()
            .clickOutsideToClose(true)
            .title('Left group!')
            .ariaLabel('Left Group Confirmation')
            .ok('Okay!')
        );

        $state.go('private.main');
      }, function (res) {
        $mdDialog.show(
          $mdDialog.alert()
            .clickOutsideToClose(true)
            .title('Could not leave group.')
            .textContent(res.data)
            .ariaLabel('Can\'t leave group')
            .ok('Okay!')
        );
        console.log(res.data);
      });

    };

  }

})();

(function() {
  'use strict';

  angular
    .module('geldzakenAngular')
    .controller('PaymentsController', PaymentsController)
    .controller('CreatePaymentController', CreatePaymentController);

  /** @ngInject */
  function PaymentsController($mdDialog, $mdMedia, $state, $stateParams, paymentService, group) {
    var ctrl = this;

    ctrl.group = group;

    ctrl.showCreatePayment = function ($event) {
      var useFullScreen = ($mdMedia('sm') || $mdMedia('xs'));
      $mdDialog.show({
        controller: CreatePaymentController,
        controllerAs: 'ctrl',
        templateUrl: 'app/group/payments/createPayment.html',
        targetEvent: $event,
        clickOutsideToClose: true,
        fullscreen: useFullScreen,
        locals: {
          group: ctrl.group
        }
      })
      .then(function(payment) {
        paymentService.Payment.save({ id: group.id }, payment, function (data) {
          console.log('Success!');
          console.log(data);

          $mdDialog.show(
            $mdDialog.alert()
              .clickOutsideToClose(true)
              .title('Payment was added!')
              .ariaLabel('Payment Confirmation')
              .ok('Okay!')
          );

          $state.reload();
        }, function (data) {
          console.log('Error');
          console.log(data);
        });
      });
    };

    ctrl.openPaymentOptions = function (ev) {
      $mdDialog.show(
        $mdDialog.confirm()
          .clickOutsideToClose(true)
          .title('Delete payment')
          .textContent('Are you sure you want to delete this payment?')
          .ariaLabel('Payment Options')
          .targetEvent(ev)
          .ok('Delete')
          .cancel('Cancel')
      );
    };

    ctrl.openMenu = function($mdOpenMenu, ev) {
      $mdOpenMenu(ev);
    };

    ctrl.sorting = '-createdAt';

    ctrl.participants_to_string = function (participants) {
      var names = [];
      for (var i = 0; i < participants.length; i++) {
        var p = participants[i];
        var weight = p.paymentParticipation.weight;
        if (weight > 1) {
          names.push(p.name + ' (' + weight + 'x)');
        } else {
          names.push(p.name);
        }
      }

      // Maak er een zin van.
      if (names.length == 1)
        return names[0];
      var last = names.pop();
      return names.join(', ') + ' and ' + last;
    };

    ctrl.go = function (state) {
      $state.go('^.'+state, $stateParams);
    };
  }

  /** @ngInject */
  function CreatePaymentController($mdDialog, $timeout, $q, group, userService) {
    var ctrl = this;
    ctrl.group = group;
    ctrl.payment = {
      participants: [],
      payedBy: userService.me().email
    };

    ctrl.participants = (function () {
      var participants = _.map(group.members, function (p) {
        return {
          email: p.email,
          name: p.name,
          weight: 0
        }
      });
      return participants;
    })();

    ctrl.increaseAllParticipants = function () {
      ctrl.participants.forEach(ctrl.increaseParticipant);
    };

    ctrl.resetAllParticipants = function () {
      ctrl.participants.forEach(function (p) {
        p.weight = 0;
      });
    };

    ctrl.increaseParticipant = function (participant) {
      participant.weight++;
    };

    ctrl.decreaseParticipant = function (participant) {
      if (participant.weight > 0)
        participant.weight--;
    };

    ctrl.cancel = function() {
      $mdDialog.cancel();
    };

    ctrl.create = function() {
      ctrl.participants.forEach(function (p) {
        if (p.weight > 0) {
          ctrl.payment.participants.push({
            email: p.email,
            weight: p.weight
          });
        }
      });

      $mdDialog.hide(ctrl.payment);
    };
  }
})();

(function() {
  'use strict';

  angular
    .module('geldzakenAngular')
    .controller('MembersController', MembersController)
    .controller('InviteMemberController', InviteMemberController);

  /** @ngInject */
  function MembersController($mdDialog, $mdMedia, $state, $stateParams, groupService, userService, memberService, group, balanceCalculator) {
    var vm = this;

    vm.group = group;

    vm.showInviteMember = function ($event) {
      $mdDialog.show({
        controller: InviteMemberController,
        controllerAs: 'vm',
        templateUrl: 'app/group/members/inviteMember.html',
        targetEvent: $event,
        clickOutsideToClose: true
      })
      .then(function(member) {
        memberService.Member.save({ group: group.id }, member, function (data) {
          console.log('Success!');
          console.log(data);

          $mdDialog.show(
            $mdDialog.alert()
              .clickOutsideToClose(true)
              .title('Member was invited!')
              .ariaLabel('Invitation Confirmation')
              .ok('Okay!')
          );

          $state.reload();
        }, function (data) {
          console.log('Error');
          console.log(data);
        });
      });
    };

    var balances = balanceCalculator.calculate(group.members, group.payments);

    vm.group.members.forEach(function (member) {
      member.balance = balances[member.email];
    });

    vm.openMenu = function($mdOpenMenu, ev) {
      $mdOpenMenu(ev);
    };

    vm.sorting = '-balance';
  }

  /** @ngInject */
  function InviteMemberController($mdDialog) {
    var vm = this;
    vm.email = '';

    vm.cancel = function() {
      $mdDialog.cancel();
    };

    vm.invite = function() {
      $mdDialog.hide({ email: vm.email });
    };
  }
})();

