(function() {
  'use strict';

  angular
    .module('geldzakenAngular')
    .directive('loadingIndicator', loadingIndicator);

  function loadingIndicator () {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/loadingIndicator/loadingIndicator.html',
      scope: {},
      controller: loadingIndicatorController,
      controllerAs: 'ctrl',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function loadingIndicatorController($rootScope) {
      var ctrl = this;

      $rootScope.$on('$stateChangeStart', function () {
        ctrl.show = true;
      });
      $rootScope.$on('$stateChangeSuccess', function () {
        ctrl.show = false;
      });

      $rootScope.$on('http:request', function () { 
        ctrl.show = true;
      });
      $rootScope.$on('http:response', function () { 
        ctrl.show = false;
      });
      $rootScope.$on('http:responseError', function (event, data) {
        ctrl.show = false;
      });
    };

  }

})();

(function() {
  'use strict';

  angular
      .module('geldzakenAngular')
      .factory('httpInterceptor', httpInterceptor);

  /** @ngInject */
  function httpInterceptor($rootScope, $q) {
    return {
      request: function (config) {
        $rootScope.$broadcast('http:request')
        return config
      },
      response: function (response) {
        $rootScope.$broadcast('http:response')
        return response
      },
      responseError: function (responseError) {
        $rootScope.$broadcast('http:responseError', responseError)
        return $q.reject(responseError);
      }
    }
  }

})();

