(function() {
  'use strict';

  angular
    .module('geldzakenAngular')
    .controller('LoginController', LoginController);

  /** @ngInject */
  function LoginController(authorization, authentication, bcrypt) {
    var ctrl = this;

    ctrl.user = {email: '', password: ''}

    ctrl.login = function () {
      if (ctrl.user == undefined || ctrl.user.email == undefined || ctrl.user.password == undefined) {
        throw new Error("You must enter a valid e-mailadress and password.");
      } else {
        authentication.requestToken(ctrl.user.email, ctrl.user.password);
      }
    };

    console.log('LoginController loaded. Let\'s fire authentication.init()');
    authentication.processSavedCredentials();
  }
})();

(function() {
  'use strict';

  angular
    .module('geldzakenAngular')
    .controller('GroupMainController', GroupMainController);

  /** @ngInject */
  function GroupMainController($rootScope, $mdDialog, $state, $stateParams, groupService, userService, group) {
    var ctrl = this;

    ctrl.group = group;

    ctrl.go = function (state) {
      $state.go('private.group.' + state, $stateParams);
    };

    ctrl.setCurrentTab = function (state) {
      if (state.data && state.data.selectedTab)
        ctrl.currentTab = state.data.selectedTab;
    };

    $rootScope.$on('$stateChangeSuccess', function(event, toState) {
      ctrl.setCurrentTab(toState);
    });

    ctrl.setCurrentTab($state.current);
  }
})();

