(function() {
  'use strict';

  angular
    .module('geldzakenAngular')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
    .state('public', {
      templateUrl: 'app/public/public.html',
      controller: 'PublicController as ctrl',
      anonymous: true
    })
    .state('public.login', {
      url: '/login',
      templateUrl: 'app/login/login.html',
      controller: 'LoginController as ctrl',
      resolve: {
        $title: function() {
          return 'Login';
        }
      }      
    })
    .state('public.signup', {
      url: '/signup',
      templateUrl: 'app/signup/signup.html',
      controller: 'SignupController as ctrl',
      resolve: {
        $title: function() {
          return 'Sign up';
        }
      }      
    })
    .state('public.logout', {
      url: '/logout',
      templateUrl: 'app/logout/logout.html',
      controller: 'LogoutController as ctrl',
      resolve: {
        $title: function() {
          return 'Logout';
        }
      }      
    })

    .state('private', {
      templateUrl: 'app/private/private.html',
      controller: 'PrivateController as ctrl',
      authorized: true
    })
    .state('private.main', {
      url: '/',
      templateUrl: 'app/main/main.html',
      controller: 'MainController as main',
      authorized: true,
      resolve: {
        $title: function() {
          return 'Overview';
        }
      }
    })
    .state('private.settings', {
      url: '/settings',
      templateUrl: 'app/settings/settings.html',
      controller: 'SettingsController as vm',
      authorized: true,
      resolve: {
        $title: function() {
          return 'Settings';
        }
      }
    })
    .state('private.createGroup', {
      url: '/groups/new',
      templateUrl: 'app/group/createGroup.html',
      controller: 'CreateGroupController as ctrl',
      authorized: true,
      resolve: {
        $title: function () {
          return 'Create a group';
        }
      }
    })
    .state('private.group', {
      abstract: true,
      url: '/groups/:id',
      templateUrl: 'app/group/main.html',
      controller: 'GroupMainController as ctrl',
      authorized: true,
      resolve: {
        group: function ($stateParams, groupService) {
          return groupService.Group.get({group: $stateParams.id}).$promise;
        },
        $title: function (group) {
          return group.name;
        }
      }
    })
    .state('private.group.payments', {
      url: '/payments',
      templateUrl: 'app/group/payments/payments.html',
      controller: 'PaymentsController as ctrl',
      authorized: true,
      data: {
          'selectedTab' : 0
      }
    })
    .state('private.group.members', {
      url: '/members',
      templateUrl: 'app/group/members/members.html',
      controller: 'MembersController as ctrl',
      authorized: true,
      data: {
          'selectedTab' : 1
      }
    })
    .state('private.group.settle', {
      url: '/settle',
      templateUrl: 'app/group/settle/settle.html',
      controller: 'SettleController as ctrl',
      authorized: true,
      data: {
          'selectedTab' : 2
      }
    })
    .state('private.group.settings', {
      url: '/settings',
      templateUrl: 'app/group/settings/settings.html',
      controller: 'GroupSettingsController as vm',
      authorized: true,
      data: {
          'selectedTab' : 3
      }
    });

    $urlRouterProvider.otherwise('/');
  }

})();

