(function() {
  'use strict';

  angular
    .module('geldzakenAngular')
    .config(config);

  /** @ngInject */
  function config($provide, $httpProvider, $logProvider, $mdThemingProvider, toastrConfig, localStorageServiceProvider) {
    $mdThemingProvider.theme('default')
    .primaryPalette('indigo')
    .accentPalette('grey');

    $provide.decorator("$exceptionHandler", function($delegate) {
      return function (exception, cause) {
        console.log(exception);
      };
    });

    $httpProvider.interceptors.push('httpInterceptor');

    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;

    // Set prefix for localStorage
    localStorageServiceProvider.setPrefix('geldzaken');
    localStorageServiceProvider.setStorageCookie(100, '/');
  }

})();

(function() {
  'use strict';

  angular
    .module('geldzakenAngular')
    .controller('CreateGroupController', CreateGroupController);

  /** @ngInject */
  function CreateGroupController($state, groupService) {
    var ctrl = this;

    ctrl.group = {}

    ctrl.create = function () {
      var group = new groupService.Group({ name: ctrl.group.name });
      group.$save(function(data) {
        $state.reload();
        $state.go('private.group.participants', { id: data._id })
      });
    };
  }
})();

